<template>
  <div class="search_tree">
    <div class="f26 c36c748 bold">
      </div>
    <searchInput></searchInput>
    <list :search="trees" :Ttype="'tree'" v-if="trees.length>0"></list>
    <!--暂无数据-->
    <div class="f25 center mT100 mB100 c999999" v-if="loading==0">加载中，请稍后片刻...</div>
    <div class="f25 center mT100 mB100 c999999" v-if="loading==1">暂无数据，可尝试其它的筛选条件</div>

    <div style="width:1220px;" class="margin bai">
       <div id="pages" class="pT30 pB20 pL30 margin" ></div>
    </div>

    <div v-show="0">{{watch}}</div>
  </div>
</template>

<script>
  import searchInput from "./ym_type.vue";
  import list from "./list.vue";
  export default {
    data() {
      return {
        searchKey: "",
        trees: [],
        loading:0//0是加载中，1是暂无数据，2是有数据
        // page:1
      }
    },
    components: {
      "list": list,
      "searchInput": searchInput,
    },
    computed: {
      watch() {
        // this.trees=this.$store.state.trees;
        // let that = this;
        // if(this.searchkey != this.$route.params.se){
        //     this.searchKey = this.$route.params.se;
        //     this.getdata(this.searchKey);
        // }
      }
    },
// list组价的activated钩子
 activated() {
 	// alert('B页面的activated')
    // isUseCache为false时才重新刷新获取数据
    // 因为对list使用keep-alive来缓存组件，所以默认是会使用缓存数据的
    if(!this.$route.meta.isUseCache){
    // alert('B页面的activated重新请求数据')
         this.trees = []; // 清空原有数据
        this.pages(1); // 这是我们获取数据的函数
        this.$route.meta.isUseCache = false;
    }
},
 deactivated() {

},
		// B页面离开的时候B的路由属性
beforeRouteLeave (to, from, next) {
      from.meta.isUseCache=true
    // if (to.name == 'mehome') {
    //     from.meta.isUseCache = false;
    //     // alert('B页面离开的时候设置A页面的使用缓存为true')
    // }
    next();
},
		// 进入B页面的时候设置本页面是否需要缓存
beforeRouteEnter (to, from, next) {
    // console.log(from.name)
    if(from.name=='mehome'){
      to.meta.isUseCache=false
    }
    next();
},
// 		// B页面到A页面
// beforeRouteLeave (to, from, next) {
//     if (to.name == 'HOME') {
//         to.meta.isUseCache = true;
//         alert('B页面离开的时候设置A页面的使用缓存为true')
//     }
//     next();
// },
    methods: {
      https(page){
        var that=this
        // 获取搜索记录到localStorage
        // var datas
        // var searchQuery = window.localStorage.getItem('searchQuery');
        // if (searchQuery) {
        //   datas = {
        //     'searchkey': JSON.parse(searchQuery).searchkey,
        //     'ispublish': 1,
        //     'sort': JSON.parse(searchQuery).sort,
        //     'page': page,
        //     'limit':20,
        //     'where': JSON.parse(searchQuery).where
        //   }
        // } else {
        //   datas = {
        //     'searchkey': this.$route.params.se || '',
        //     'ispublish': 1,
        //     'limit':20,
        //     'page':page
        //   }
        // }
var datas = {
          'searchkey': this.$parent.searchkey||this.$route.params.se||'',
          'ispublish': this.$parent.ispublish,
          'sort': this.$parent.sort,
          'page': this.$parent.page,
           'limit':limit40S,
          'where': JSON.stringify(this.$parent.where)

        }

        $.ajax({
          url: that.$store.state.v1 + "tree",
          type: "GET",
          data: datas,
          headers: {
            "role": "seller",
          },
          success:function(data){
                if(data.status){
                 that.trees=data.results.data;
                 if(that.trees.length>0){
                 that.loading=2
                 }else{
                   that.loading=1
                 }

                //  that.count=data.results.count
                }else{
                   layui.use('layer', function(){
                      var layer = layui.layer;
                      layer.msg(data.message);
                      });
                }
          }
        })

    },
       pages(page){
      //首次请求数据，并构建分页
      let that = this;
        // 获取搜索记录到localStorage
        // var datas
        // var searchQuery = window.localStorage.getItem('searchQuery');
        // if (searchQuery) {
        //   datas = {
        //     'searchkey': JSON.parse(searchQuery).searchkey,
        //     'ispublish': 1,
        //     'sort': JSON.parse(searchQuery).sort,
        //     'page': page,
        //     'limit':20,
        //     'where': JSON.parse(searchQuery).where
        //   }
        // } else {
        //   datas = {
        //     'searchkey': this.$route.params.se || '',
        //     'ispublish': 1,
        //     'limit':20,
        //     'page':page
        //   }
        // }
// var datas = {
//           'searchkey': this.$parent.searchkey||this.$route.params.se||'',
//           'ispublish': this.$parent.ispublish,
//           'sort': this.$parent.sort,
//           'page': this.$parent.page,
//           'limit':limit40S,
//           'where': JSON.stringify(this.$parent.where)

//         }

var datasno = {
            'searchkey': this.$route.params.se || '',
            'ispublish': 1,
            'limit':limit40S,
            'page':page
          }
var datas=this.$route.params.queryData=='no'?datasno:JSON.parse(this.$route.params.queryData);
        $.ajax({
          url: that.$store.state.v1 + "tree",
          type: "GET",
          data: datas,
          headers: {
            "role": "seller",
          },
          success:function(data){
                if(data.status){
                 that.trees=data.results.data;
                   if(that.trees.length>0){
                 that.loading=2
                 }else{
                   that.loading=1
                 }
                 var count=data.results.count;
                 layui.use('laypage', function(){
                  var laypage = layui.laypage;
                  //执行一个laypage实例
                  laypage.render({
                    elem: 'pages', //注意，这里的 test1 是 ID，不用加 # 号
                    limit:limit40S,
                    // curr:location.hash.replace('?!fenye=', ''), //获取起始页,
                    // hash: 'fenye', //自定义hash值
                    layout:['prev', 'page', 'next','count', 'skip'],
                     prev:'<i class="layui-icon f12 mR05" style="">&#xe603;</i>上一頁',
                    next:'下一頁<i class="layui-icon f12 mL05" >&#xe602;</i>',
                    // hash: 'page', //自定义hash值
                    count:count, //数据总数，从服务端得到
                    jump:function(obj,first){
                    // alert(obj.curr)
                    //当分页被切换时触发，函数返回两个参数：obj（当前分页的所有选项值）、first（是否首次，一般用于初始加载的判断）
                    //如果是切换页
                    if(!first){
                       console.log('切换对应页数据')
                          that.page=obj.curr
                          that.https(obj.curr)
                    }else{
                      // 如果是首次
                       console.log('首次啥也不干')
                        //that.https(1)
                    }
                  }
                });
              });
                }
          },
          error:function(data){

          },
        })
    }
    },
    mounted: function () {
      // alert('mounted')
      this.pages(1)
      // if(this.$route.name != "no_query_search"){
      // this.getdata(this.$route.params.se || '')
      // }

    }
  }

</script>
<style lang="less">

</style>



