          <div>
            <p>{{tree.mu_name}}</p>
            <div>
              <div v-for="(item,index) in show_arr">
                <span>{{item.name}}</span>
                <span>:</span>
                <span>{{tree[item.zd]}}</span>
              </div>
              <div v-for="(item,index) in tree.show">
                <span class="tleft">{{item.name}}</span>
                <span>:</span>
                <span>{{item.data}}</span>
              </div>
              <div>
                <span class="tleft">{{"常用名"}}</span>
                <span>:</span>
                <span>{{tree.common_names}}</span>
              </div>
              <div>
                <span class="tleft">{{"创建时间"}}</span>
                <span>:</span>
                <span>{{tree.createdAt}}</span>
              </div>
            </div>
            <div>
              <div>
                <span>价格：</span>
                <span>{{tree.mu_price==-1?"面议":"￥"+tree.mu_price}}</span>
                <span>{{"(库存"+tree.mu_stock+tree.unit+")"}}</span>
              </div>
            </div>

          </div>
        </div>
        <div>
          <div class="tt">
            <div></div>


