                <div class="w250">
                  <div class="radius50 w70 h70 overflow margin"><img :src="tree.user.avatar.url+imageView2" class="cursor w100s"></div>
                  <p class="f15 mT15 mB05">发布者：<span class="f20">{{tree.user.nickname}}
                              <!--<i class="call inline w50 h50" style=" background-size:50px 50px;" @click="call_sel(tree.user)"></i>-->
                              <img class="call w30 h30 cursor" src="/static/img/common/call.png"  @click="call_sel(tree.user)">
                              </span></p>
