<script>
  import same from "./attr/same.vue";
  export default {
    data() {
      return {
        // changeds:true,
        // isfav:false,
        imageView2: '?imageView2/1/w/70/h/70',
        tree: "",
        srcc: "",
        el_bool: { now_index: 0 },
        show_img_url: "",
        show_arr: [{ name: "种类", zd: "mu_type" },
        { name: "种植情况", zd: "mu_plant_type" },
        { name: "地址", zd: "mu_city" }
        ],
        searchK: "",
        trees: [],
      }
    },


    // 		// 详情页面的beforeRouteLeave钩子函数
    // beforeRouteLeave (to, from, next) {
    //   console.log(to,from)
    //     if (to.name == 'no_query_search_tree') {
    //         to.meta.isUseCache = true;
    //         // to.meta.isfav = this.isfav;
    //         // alert('C页面离开的时候设置B页面的使用缓存为检测')
    //     }
    //     next();
    // },
    components: {
      "same": same,
    },
    computed: {
      watch() {
        if (this.$route.params.id != this.tree.objectId) {
          this.gettree();
        }
      }
    },
    methods: {
      getTs() {
        let that = this;

        $.ajax({
          url: that.$store.state.v1 + "ground/" + this.tree.mu_ground.objectId + "/tree",
          data: { limit: 7, page: 1 },
          timeout: 15000,
          type: "GET",
          success: function (data1) {
            console.log("更多苗木获取：成功");
            that.trees = [];
            $.each(data1.results.data, function (index) {
              if (that.trees.length < 6) {
                if (that.tree.objectId != this.objectId) {
                  that.make_show_str(this);
                  let makestr = this.mu_name + "(";
                  let show = this.show;
                  for (let index in show) {
                    makestr += show[index].name + ":" + show[index].data;
                  }
                  makestr += ")";
                  this["show1"] = makestr;
                  that.trees.push(this);
                }
              }
            })
            $(".call").css("background", `url(${that.srcc}/static/img/common/call.png) no-repeat`);
          }
        })
      },
      tInput(e) {
        let ee = $(e.currentTarget);
        let that = this;
        if (e.keyCode == 13 || e == undefined) {
          if (that.searchK != "") {
            this.$router.push({ name: "query_search", params: { se: that.searchK } });
          }
        }
      },
      //联系卖家
      call_sel(user) {
        let that = this;
        if (localStorage.getItem("user") == null) {
          this.$store.state.login_el_bool = true;
        } else {
          // 点击头像调用对话函数
          otherChats(user)

        }


      },
      img_load(e) {
        let ee = $(e.currentTarget);
        let ew = ee.width();
        let eh = ee.height();
        let pw = ee.parent().width();
        let ph = ee.parent().height();
        if (pw > ew && ph == eh) {
          ew = pw;
          ee.css({ width: ew + "px", height: "auto" })
          eh = ee.height();
        } else if (ph > eh && pw == ew) {
          eh = ph;
          ee.css({ height: eh + "px", width: "auto" })
          ew = ee.width();
        }
        ee.css({
          bottom: (eh - ph) / 2 + "px",
          right: (ew - pw) / 2 + "px"
        })
      },
      change_img(_url) {
        let that = this;
        that.show_img_url = _url;
      },
      make_show_str(tree) {
        let that = this;
        let jsond = that.$store.state.jsondata;
        let _tree = tree;
        let _type = jsond.type[_tree.mu_type];
        let _strs = ["required", "optional"];
        let _show = [];
        for (let i = 0; i < _strs.length; i++) {
          _type[_strs[i]].forEach((val) => {
            let _atype = jsond.company[val];
            let show_d = "";
            if (_atype.status == 1) {
              let min = _tree["mu_" + _atype.name + "_min"];
              let max = _tree["mu_" + _atype.name + "_max"];
              if (min) {
                show_d = min + _atype.unit;
                if (min != max) {
                  show_d = min + "-" + max + _atype.unit;
                }
              }
            } else if (_atype.status == 0 || _atype.status == 2) {
              show_d = _tree["mu_" + _atype.name];
            } else if (_atype.status == "no") {
              show_d = _tree["mu_" + _atype.name];
              if (show_d) {
                let _b = false;
                for (let key in _atype.exception.name) {
                  if (key == val) {
                    _b = true;
                  }
                }
                show_d += !_b ? _atype.unit : _atype.exception.unit;
              }

            }
            if (show_d) {
              _show[_show.length] = { name: val, data: show_d };
            }
          })
        }
        tree["show"] = _show;
      },
      router_go(where, id) {
        let that = this;
        that.$router.push({ name: where, params: { "id": id } })
      },
      gettree() {
        let that = this;
        $.ajax({
          url: that.$store.state.v1 + "tree/" + that.$route.params.id,
          type: "GET",
          success: (data) => {
            console.log(data)
            that.tree = data.results;
            that.tree["cd"] = that.tree.mu_province + that.tree.mu_city;
            that.show_img_url = that.tree.mu_photo[0].url;
            that.make_show_str(that.tree);
            $(".call").css("background", `url(${that.srcc}/static/img/common/call.png) no-repeat`);
            that.getTs();
          },
        })
      },
      top_img_load(e) {
        let ee = $(e.currentTarget);
        let _width = ee.width();
        let _height = ee.height();
        console.log(e)
        ee.css("bottom", (_height - 100) / 2 + "px");
      },
      tinit() {
        let carr = { 3: ee.val().length, };
        let that = this;
        setTimeout(() => {
          $.each($(".tleft"), index => {
            let ee = $(this);
            let len = ee.val().length;
            ee.css("letter-spacing", carr[len]);
          })
        }, 500)
      }
    },
    mounted: function () {
      let that = this;
      that.srcc = this.$store.state.srcc;
      setTimeout(function () {
        // url(/static/img/common/ico.png) no-repeat rgb(249, 249, 250);
        $(".call").css("background", `url(${that.srcc}/static/img/common/call.png) no-repeat`);
      }, 1500)
      that.tree = JSON.parse(localStorage.getItem("show_tree"));
      that.tree["cd"] = that.tree.mu_province + that.tree.mu_city;
      that.show_img_url = that.tree.mu_photo[0].url;
      console.log(that.tree);
      that.make_show_str(that.tree);
      if (!that.tree) {
        // that.gettree();
        console.log("需要调用接口")
        this.gettree();
      } else {
        this.getTs();
      }
      // that.tinit();


    }
  }

</script>

