        background: white;
        &>div:nth-of-type(1){

            width: 1200px;
            margin:0 auto;
            padding: 0 45px;
            &>div:nth-of-type(1){
                // margin: 66px auto 0;
                // padding-top:66px;
                width: 100%;
                max-width: 1400px;
                &>div:nth-of-type(1){
                    position: relative;
                    &>ul{
                        display: block;
                        border-bottom: 1px solid#E7E8E9;
                        line-height: 60px;
                        padding: 0 9px 10px;
                        margin: 0 10px;
                        &>li{
                            display: inline-block;
                            line-height: 25px;
