                    }
                    &>div:nth-of-type(1){
                        position: absolute;
                        bottom: 10px;
                        right: 30px;
                        border: 1px solid #F1F1F1;
                        border-radius: 3px;
                        overflow: hidden;
                        &>input{
                            border: none;
                            padding: 0 10px;
                            color: #777;
                            font-size: 15px;
                            line-height: 35px;
                            vertical-align: middle;
                        }
                        &>span:nth-of-type(1){
                            display: inline-block;
                            line-height: 35px;
                            vertical-align: middle;
                            padding: 0 18px;
                            background: #60B346;
                            color: white;
                            float: right;
                            cursor: pointer;
                        }
                    }
                }
            }


            &>div:nth-of-type(2){
                width: 1400px;
                overflow: hidden;
                height:30px;
                margin: 0 auto;
                position: relative;
                &>img:nth-of-type(1){
                    width: 100%;
                    position: relative;
                }

            }
            &>div:nth-of-type(3){
                width: 100%;
                margin: 5px auto 0;
                height: 520px;
                &>div{
                    float:left;
                }
                &>div:nth-of-type(1){
                    margin-left: 7px;
                    padding: 11px 10px;
                    background: white;
                    box-shadow: 0 0 10p x rgba(0, 0, 0, 0.1);
                    width: 300px;
                    height: 436px;
                    &>div:nth-of-type(1){
                        position: relative;
                        overflow: hidden;
                        height: 363px;
                        border: 1px solid #E8E8E8;
                        &>img:nth-of-type(1){
                            height:100%;
                            position: relative;
                        }
                    }
                    &>div:nth-of-type(2){

                        margin-top: 5px;
                        position: relative;
                        &>ul:nth-of-type(1){

                            width: 300px;
                            overflow: hidden;
                            height: 65px;
                            &>li{

                                width:60px;
                                height:60px;
                                overflow: hidden;
                                position: relative;
                                border: 1px solid #E8E8E8;
                                margin-left: 13px;

                                &:hover{
                                    box-shadow: 0 0 10px rgba(0,0,1,0.5);
                                }

                                &:nth-of-type(1){
                                    margin-left: 0px;
                                }

                                &>img{
                                    width: 100%;
                                    position: relative;
                                }
                            }
                        }
                    }
                }

                &>div:nth-of-type(2){
                    margin-left: 15px;
                    border-top:2px solid #F2F2F2;
                    // box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
                    height: 443px;
                    background: white;
                    width:536px;
                    &>div:nth-of-type(1){
                        padding: 15px 10px;

                        &>p:nth-of-type(1){
                            font-weight: 500;
                            font-size: 25px;
                            margin-top: 10px;
                        }
                        &>p:nth-of-type(2){
                            font-size:15px;
                            color:#777;
                            margin: 10px 0 1px;
                        }
                        &>p:nth-of-type(3){
                            font-size:15px;
                            margin: 10px 0 1px;
                        }
                        &>div:nth-of-type(1){
                            background: #F5F5F5;
                            padding: 5px 20px;
                            color: #999999;
                            margin-top: 20px;
                            &>div{
                                margin: 13px 0;
                                font-size: 14px;
                                &>span{
                                    display: inline-block;
                                    overflow: hidden;
                                    &:nth-of-type(1){
                                        width:60px;
                                    }
                                    &:nth-of-type(2){
                                        margin: 0 15px;
                                    }
                                    &:nth-of-type(3){
                                        margin-left: 15px;
                                        text-overflow: ellipsis;
                                        white-space: nowrap;
                                        width: 340px;
                                    }
                                }
                            }

                        }
                        &>div:nth-of-type(2){
                            padding: 10px 8px;
                            &>div{
                                    font-size:20px;
                                    margin-top:11px;
                                    color: #777;
                                &:nth-of-type(1){
                                    &>span:nth-of-type(2){
                                        color: #ff2b2b;
