                                    }
                                }
                            }
                        }
                    }
                }
                &>div:nth-of-type(3){
                    width: 320px;
                    // box-shadow: 0 0 10px rgba(0,0,1,0.5);
                    &>div:nth-of-type(1){
                        margin: 0 5px 0 20px;
                        border: 1px solid #dcdada;
