<template>
     <router-link  :to="{name:'query_tree',params:{id:tree.objectId}}"  target="_blank">
     <div @click="show_tree()" class="tree left" :class="{h403:isground,showbr:el_bool.all_br}" @mouseenter="change_bool('all_br',true)" @mouseleave="change_bool('all_br',false)">
        <div v-if="tree">
            <div>
                <img :src="tree.mu_photo.length>0?tree.mu_photo[0].url+imageView2:''"  draggable="false">
            </div>
            <div>
                <span>{{tree.mu_name+"-"}}</span>
                <span v-if="tree.mu_plant_type">{{tree.mu_plant_type.replace("苗","")}}</span>
                <span>{{tree.mu_city}}</span>
                <p v-for="(item,index) in tree.showstr">{{item}}</p>
                <div>
                    <span>{{tree.mu_price==-1?"面议":tree.mu_price+"元"}}</span>
                    <span>{{"库存："+tree.mu_stock+tree.unit}}</span>
                </div>
            </div>
            <div>
                <div>
                    <img :src="tree.user.avatar.url" alt="苗讯" draggable="false">
                </div>
                <span>{{tree.mu_ground.gg_name}}</span>
            </div>
        </div>
    </div>
    </router-link>
</template>

<script>
    export default {
        data(){
            return{
                imageView2:'?imageView2/1/w/207/h/276',
                el_bool:{all_br:false}
            }
        },
        props:{
            tree:{
                type:Object,
                "default":function(){
                    return {};
                }
            },
            isground:{
                type:Boolean,
                "default":function(){
                    return false;
                }
            },
        },
        methods:{

            show_tree(){
                let that = this;
                // window.open(URL+'query/tree/'+that.tree.objectId)
                // this.$router.push({name:"query_tree",params:{id:that.tree.objectId}});
                localStorage.setItem("show_tree",JSON.stringify(that.tree));
            },
            change_bool(what,_bool){
                let that = this;
                this.el_bool[what] = _bool;

            },
        },
        mounted:function(){
        }
    }
</script>

<style lang="less" scoped>
    .h403{
        height:403px !important;
    }
    .showbr{
        /*border:1px solid #B2421B !important;*/
    }
    .tree{
        cursor: pointer;
        background: white;
        width: 224px;
        display: inline-block;
        border-radius: 6px;
        margin: 0 20px 20px 0;
        height: 447px;
        border: 2px solid #F4F5F7;
        overflow: hidden;
        &>div:nth-of-type(1){

            &>div:nth-of-type(1){
                width: 203px;
                margin: 10px auto;
                height: 276px;
                overflow: hidden;
                // border-radius: 6px;
                &>img:nth-of-type(1){
                    height:100%;
                    position: relative;
                }
            }

            &>div:nth-of-type(2){

                position: relative;
                padding: 0px 16px 7px;
                height: 100px;
                &>span:nth-of-type(1){
                    display: inline-block;
                    font-size: 20px;
                    vertical-align: middle;
                    color:#333;
                    max-width: 150px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }

                &>span:nth-of-type(2){
                    display: inline-block;
                    font-size:14px;
                    color:#60B346;
                    vertical-align: middle;
                    position: relative;
                    right: 4px;
                }
                &>span:nth-of-type(3){
                    display: inline-block;
                    font-size:14px;
                    color:#BBBBBB;
                    float:right;

                }
                &>p{
                    font-size:12px;
                    color: #999899;
                    margin: 0;
                }
                &>p:nth-of-type(1){
                    margin-top: 2px !important;
                }
                &>p:nth-of-type(2){
                    // margin-top: 3px !important;
                }
                &>div:nth-of-type(1){

                    position: absolute;
                    bottom: 6px;

                    &>span:nth-of-type(1){
                        color: #74B22B;
                        font-size:17px;
                    }
                    &>span:nth-of-type(2){
                        color: #999899;
                        font-size:13px;
                        display: inline-block;
                        margin-left: 13px;
                    }
                }
            }

            &>div:nth-of-type(3){
                border-top: 1px solid #EEEEEE;
                width: 200px;
                padding: 9px 13px;
                &>div:nth-of-type(1){
                    width:24px;
                    height:24px;
                    overflow: hidden;
                    border-radius: 25px;
                    display:inline-block;
                    vertical-align: middle;
                    &>img:nth-of-type(1){
                        width:24px;
                    }
                }
                &>span:nth-of-type(1){
                    display: inline-block;
                    vertical-align: middle;
                    font-size: 15px;
                    color: #4A494B;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    width: 170px;
                }
            }

        }
        &:hover{
            box-shadow: 0 0 10px rgba(0,0,0,0.2);
        }
    }
</style>



