<template>

  <div id="app">
    <!--<transition :name="this.$store.state.transitioner">-->

      <!--<router-view ></router-view>-->

<!--<keep-alive>-->
    <!--<router-view v-if="$route.meta.keepAlive"/>-->
    <router-view/>
<!--</keep-alive>-->
<!--<router-view v-if="!$route.meta.keepAlive"/>-->


    <!--</transition>-->
    <chat v-if="el_bool.chat"></chat>
     <bottom class="mT50"></bottom>
<!--跟目录加这个标签容器，才能在整个网站调用图片放大-->
      <!--<lg-preview></lg-preview>-->
  </div>

</template>

<script>
import chat from "./components/common/chat.vue";
import bottom from "./components/common/bottom.vue";
export default {
  name: 'app',
  data(){
    return{
      el_bool:{chat:false}
    }
  },
  components:{
    "chat":chat,
    'bottom':bottom
  },
  mounted:function(){
    let that = this;
    // 1.5秒后加载 聊天
    setTimeout(function(){
      that.el_bool.chat = true;
    },1500);
    // let user = localStorage.setItem("user");
    // if(user!=null&&user!='null'){
    //   this.$store.state.user = user;
    // }
  }
}
</script>

<style >
@import './assets/css/common.css';
@import './assets/css/commonPc.css';
  #app{
    width:100%;
    min-width:1280px;
    margin:0 auto;
    /* background: #F2F2F2; */
  }
  .fade-enter-active {
    position:relative;
    transition: all 1s ease;
    left:0;
  }
  .fade-enter{
  	left:100%;
  }
  .fade-leave{
    left:-100%;
  }
  .fade2-enter-active {
    position:relative;
    transition: all 1s ease;
    left:0;
  }
  .fade2-enter{
  	left:-100%;
  }
  .fade2-leave{
    left:100%;
  }

</style>



