<template>
    <div class="same">
        <div v-for="(item,index) in trees" @click="clickT(item.objectId)">
            <div>
                <img :src="item.mu_photo[0].url" draggable="false">
            </div>
            <div>
                <span>{{item.show1}}</span>
                <p>{{item.mu_price==-1?"￥面议":("￥"+item.mu_price)}}</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            srcc:"",
        }
    },
    props:{
        trees:{
            type:Array,
            "default":function(){
                return [];
            }
        }
    },
    methods:{
        clickT(id){
            this.$router.push({name:"query_tree",params:{"id":id}})
        }
    },
    mounted:function(){
        this.srcc = this.$store.state.srcc;
    }
}
</script>

<style lang="less" scoped>
    .same{
        // height:400px;
        &>div{
            cursor: pointer;
            border: 1px solid #F2F2F2;
            padding: 5px 7px;
            border-radius: 3px;
            background: white;
            width: 146px;
            height: 212px;
            display: inline-block;
            margin-right: 10px;
            &>div{
                &:nth-of-type(1){
                    width:130px;
                    height:130px;
                    overflow: hidden;
                    position: relative;
                    margin:10px auto;
                    border: 1px solid #F2F2F2;
                    &>img:nth-of-type(1){
                        width:100%;
                        position: relative;
                    }                        
                }
                &:nth-of-type(2){
                    font-size: 12px;
                    color: #666;
                    &>span{
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 2;
                        overflow: hidden;
                    }
                    &>p{
                        font-size: 15px;
                        color: #e00;
                        font-weight: 500;
                        margin: 5px 0 3px;
                    }
                }
            }
        }
    }
</style>



